<template>
  <div class="home">
    <!-- <header>
      <van-dropdown-menu class="fl">
        <van-dropdown-item v-model="city" :options="cities" />
      </van-dropdown-menu>
      <form action="/">
        <van-search v-model="value" placeholder="请输入搜索关键词" show-action />
      </form>
    </header> -->
    <header>
      <div class="fl">{{cities.city}}</div>
      <van-search
          v-model="value"
          placeholder="请输入搜索影片的标题"
          show-action
          left-icon=" "
          @focus="search()"
        >
        <div slot="action">
          <van-icon name="search" size="20" style="margin-top:6px"/>
        </div>
      </van-search>
    </header>

    <div class="homebox">
      <!-- 轮播图 -->
      <div class="banner">
        <van-swipe :autoplay="3000" indicator-color="white" :height="180">
          <van-swipe-item v-for="(image,index) in images" :key="index">
            <img class="autoimg" v-lazy="image" />
          </van-swipe-item>
        </van-swipe>
      </div>

      <!-- 内容导航 -->
      <nav class="clearfix">
        <div v-for="(item,index) in tabbarData" :key="index">
          <a href="javascript:void(0);" @click="togglePage({name:item.name})">
            <div>
              <!-- 本地路径要添加require() -->
              <img class="autoimg" :src="item.img" />
            </div>
          </a>
          <div>{{item.title}}</div>
        </div>
        <!-- <div>
          <a href="javascript:void(0);">
            <div>
              <img class="autoimg" src="../../assets/images/video.png" />
            </div>
          </a>
          <div>最新预告</div>
        </div>

        <div>
          <a href="javascript:void(0);">
            <div>
              <img class="autoimg" src="../../assets/images/piaofang.png" />
            </div>
          </a>
          <div>实时票房</div>
        </div>

        <div>
          <a href="javascript:void(0);">
            <div>
              <img class="autoimg" src="../../assets/images/bangdan.png" />
            </div>
          </a>
          <div>历史榜单</div>
        </div>

        <div>
          <a href="javascript:void(0);">
            <div>
              <img class="autoimg" src="../../assets/images/mall.png" />
            </div>
          </a>
          <div>周边商城</div>
        </div> -->
      </nav>
      
      <div class="homecontent">
        <!-- 电影推荐 -->
        <div class="recommend">
          <div class="movieTitle clearfix">
            <span class="fl">电影推荐</span>
            <span class="fr total clearfix">
              全部({{recommendMovieData.length}})
              <i class="fr" @click="toggleRecommendMData"></i>
              <!-- <div v-if="recommendMovieData.length >10" @click="showRecommendList">
                <span>{{isHasData?'展开':'收起'}}</span>
              </div> -->
            </span>
          </div>
          <div class="movieList">
            <div class="movielist_Box">

              <div class="movie_Item" v-for="(item,index) in showRecommendMData.data" :key="index"
               :id="item.id" @click="viewMovieDetail({name: 'movieDetail', params: {id: item.id}})">
                <div class="movie_img">
                  <img
                    class="autoimg"
                    :src="item.images.small"
                    alt=""
                  />
                  <div class="grade">
                    <span>{{item.rating.average}}分</span>
                  </div>
                </div>
                <div class="one-txt movie_name">{{item.title}}</div>
                <div class="info">
                  <span class="fl">{{item.year}}年</span>
                   <span class="fr">
                     {{(item.collect_count / 10000).toFixed(1)}}万
                     <i class="fr"></i>
                  </span>
                </div>
              </div>
              
            </div>
          </div>
        </div>

        <!-- 新电影——正在热映 -->
        <div class="new_movie">
          <div class="movieTitle clearfix">
            <span class="fl">新片榜</span>
            <span class="fr total">
              全部({{newMovieData.length}})
              <i class="fr" @click="toggleNewMData"></i>
            </span>
          </div>
          <div class="movieList">
            <div class="movielist_Box">
              <div class="movie_Item" v-for="(item,index) in showNewMData.data" :key="index"
              :id="item.id" @click="viewMovieDetail({name: 'movieDetail', params: {id: item.id}})">
                <div class="movie_img">
                  <img
                    class="autoimg"
                    :src="item.images.small"
                    alt=""
                  />
                </div>
                <div class="one-txt movie_name">{{item.title}}</div>
                <!-- 评分小星星 -->
                <div class="starbox fl clearfix">
                  <!-- 未激活灰色 -->
                  <div ref="noactivestar" class="noactivestar">
                    <span>
                      <van-icon name="star" />
                    </span>
                    <span>
                      <van-icon name="star" />
                    </span>
                    <span>
                      <van-icon name="star" />
                    </span>
                    <span>
                      <van-icon name="star" />
                    </span>
                    <span>
                      <van-icon name="star" />
                    </span>
                  </div>
                  <!-- 激活黄色 -->
                  <div class="activestar" :style="{width: item.width + 'px'}">
                    <span>
                      <van-icon name="star" />
                    </span>
                    <span>
                      <van-icon name="star" />
                    </span>
                    <span>
                      <van-icon name="star" />
                    </span>
                    <span>
                      <van-icon name="star" />
                    </span>
                    <span>
                      <van-icon name="star" />
                    </span>
                  </div>
                </div>
                <div class="fl starscore">{{item.rating.average}}</div>
              </div>
            </div>
          </div>
        </div>

        <!-- 新电影——即将上映 -->
        <div class="coming_movie">
          <div class="movieTitle clearfix">
            <span class="fl">即将上映</span>
            <span class="fr total">
              全部({{comingMovieData.length}})
              <i class="fr" @click="toggleComingMData"></i>
            </span>
          </div>

          <div class="movieList">
            <div class="movielist_Box">
              <div class="movie_Item" v-for="(item,index) in showComingMData.data" :key="index"
               :id="item.id" @click="viewMovieDetail({name: 'movieDetail', params: {id: item.id}})">
                <div class="movie_img">
                  <img
                    class="autoimg"
                    :src="item.images.small"
                    alt=""
                  />
                </div>
                <div class="one-txt movie_name">{{item.title}}</div>
                <div class="buyinfo">
                  <div class="buybtn" @click.stop="buyTickets(item.id)">
                    <!-- color="linear-gradient(to right, #FF429B, #FF3472)" -->
                    <van-button round size="small" type="info" color="linear-gradient(to right, #FF429B, #FF3472)">预售</van-button>
                  </div>
                  <!-- <div>特惠</div> -->
                </div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import { createNamespacedHelpers } from "vuex";

  const { mapState } = createNamespacedHelpers("homeModule");
  export default {
    name: "movelist",

    data() {
      return {
        value: '',
      };
    },
    computed:{
      ...mapState(['cities','images','baseUrl','recommendMovieData','showRecommendMData',
      'isHasData','newMovieData','comingMovieData','showNewMData','showComingMData','isInit','tabbarData'])
    },

    methods: {

      showRecommendList(){
         if(this.isHasData){
          //提交换一换数据
           this.$store.commit('homeModule/showRecommendList');
         }
      },
      // 电影数据换一批
      toggleRecommendMData(){
         if(this.isHasData){
          //提交换一换数据
           this.$store.commit('homeModule/toggleRecommendMData');
         }
      },
      toggleComingMData(){
         if(this.isHasData){
          //  console.log('切换数据');
            
          //提交换一换数据
           this.$store.commit('homeModule/toggleComingMData');
         }
      },
      toggleNewMData(){
         if(this.isHasData){
          //提交换一换数据
           this.$store.commit('homeModule/toggleNewMData');
         }
      },

      // 相同请求统一

      //查看电影详情
      viewMovieDetail(o) {
        this.$router.push(o);
      },

      // 影片搜索
      search(){
        this.$router.push({name:'searchMovie'});
      },

      // nav路由切换
      togglePage(o){
          this.$router.push(o);
      },
      
      // 购票
      buyTickets(id){

        //获取用户登录状态
        let userStatus = localStorage.getItem('user');

        // 判断是否是登录状态
        if (userStatus){
          userStatus = JSON.parse(userStatus);

          // 没有登录
          if (!userStatus.isLogin) {
            Dialog.confirm({
                title: '您还没有登录不能购买该电影哦！',
                message:'确定将自动跳转到登录页面'
                }).then(() => {
                  return this.$router.push({name: 'login'});
            }).catch(() => {
              // on cancel
            });
          }

          // 已经登录
          this.$router.push({name:'buyTickets',query:{id}});
          
        }else{

          Dialog.confirm({
              title: '您还没有登录不能购买该电影哦！',
              message:'确定将自动跳转到登录页面'
              }).then(() => {
                return this.$router.push({name: 'login'});

            }).catch(() => {
              // on cancel
            });
          }
          this.$toast.clear();  
      },

    },

    created(){
      let self = this;

      //获取用户位置-城市定位 （腾讯API）
      $.ajax({
        type: 'GET',
        url: 'https://apis.map.qq.com/ws/location/v1/ip',
        data:{
          key:'7RHBZ-XCUWR-A63W3-WSKOU-TWDC5-5BBLP',
          output: 'jsonp'
        },
        dataType: 'jsonp',
        success: function (data) {
          // console.log('citydata=>',data);
          let city = data.result.ad_info.city.replace(/市$/, '');
          // console.log('city=>',city);
          self.$store.commit('homeModule/changeLocationCity',city);
        }
      })

      if (!this.isInit) {
        return;
      }

      // 加载提示
      this.$toast.loading({
        duration:0,
        message:'数据加载中....'
      });

      // 获取初始化数据
      this.$axios.all([
        // 推荐电影
        this.axios({
          method:"GET",
          url:this.baseUrl + '/top250'
        }).then(Recommendresult => Recommendresult.data.subjects),

        // 即将上映（要放在新榜单的前面，否则会被影响）
        this.axios({
          method:"GET",
          url: this.baseUrl + '/coming_soon'
        }).then(Comingresult => Comingresult.data.subjects),
       
        //  新榜单
       this.axios({
          method:"GET",
          url: this.baseUrl + '/new_movies'
        }).then(Newresult => {
          // this.$store.commit('homeModule/getNewMData',Newresult.data.subjects);
        
        // 分数评分关联 先添加一个属性
          Newresult.data.subjects.forEach((v) => {
            v.width = 0;
          })

          this.$store.dispatch('homeModule/getNewMData',Newresult.data.subjects)
          
          .then(() =>{
            //根据星级平均分设置宽度
            let width = this.$refs.noactivestar[0].clientWidth;
            // console.log('width ==> ', width);

            //提交changeMovielistWidth将宽度传过去
            this.$store.commit('homeModule/changeMovielistWidth', width);

            // console.log('this.showNewMData ==> ', this.showNewMData);
          })
          
        }),
        
      ]).then(this.$axios.spread((val1,val3) => {
        this.$store.commit('homeModule/getRecommendMData',val1);
        // this.$store.commit('homeModule/getNewMData',val2);
        this.$store.commit('homeModule/getComingMData',val3);

        // console.log('Recommendresult=>',val1);
        // console.log('Newresult=>',val2);
        // console.log('Comingresult=>',val3);
        this.$toast.clear();
      })
      ).catch(err => {
          // console.log('err=>',err) ;
          this.$toast.clear();
      });

    }
  };

</script>

<style lang="less" scoped>
.home {
  header{
      // height: 54px;
      padding-top: 10px;
      margin: 0 auto;
      >div{
        color: #333;
        font-size: 15px;
        padding:0 10px;
        line-height: 32px;
      }
  }

  .homebox {
    padding: 10px;
  }

  .banner {
    border-radius: 10px;
    overflow: hidden;
  }

  nav {
    padding: 20px 10px 10px;
    // background-color:lightblue;
    > div {
      float: left;
      width: 25%;
      > a {
        display: block;
        width: 50px;
        height: 50px;
        background-image: repeating-linear-gradient(
          to right,
          #fd4154,
          #fe7278,
          #fe959b
        );
        border-radius: 20%;
        margin: 0 auto;
        position: relative;

        > div {
          display: block;
          width: 26px;
          height: 26px;
          box-shadow: 1px 1px 4px 0px #fe7278;
          position: absolute;
          top: 12px;
          left: 12px;
        }
      }
      > div {
        margin: 8px auto 0;
        text-align: center;
        color: #444;
        font-size: 14px;
      }
    }
  }

  .homecontent {
    .recommend {
      margin: 10px 0;
      .movielist_Box {
        width: 1300px;
      }
      .movieList {
        .movielist_Box {
          > .movie_Item {
            > .movie_img {
              position: relative;
              border-radius: 0px;

              .grade {
                width: 55px;
                height: 30px;
                position: absolute;
                right: 4px;
                bottom: 5px;
                background-color: #fd4154;
                // border-radius: 25%;
                text-align: center;
                line-height: 30px;
                box-shadow:3px 4px 0px #ffd900;
                span {
                  text-align: center;
                  font-size: 18px;
                  color: #fff;
                  // text-shadow: 5px 2px 6px #fd4154;
                }
              }
            }
            .info{
              span{
                &:first-child{
                  text-indent: 4px;
                }
                &:last-child{
                  color: #fd4154;
                }
              }
            }
            .info span > i {
              width: 16px;
              height: 16px;
              background: url("../../assets/images/collect0.png") no-repeat
                center center;
              background-size: 16px 16px;
              margin: 0 4px;
              &:active {
                background: url("../../assets/images/collect.png") no-repeat
                  center center;
                background-size: 16px 16px;
              }
            }
          }
        }
      }
    }

    .new_movie {
      margin: 30px 0 20px;
      .movielist_Box {
        width: 770px;
      }
      .starbox {
        position: relative;
        width: 90px;
        margin-right: 5px;
        span {
          position: absolute;
          width: 18px;
          height: 18px;
          top: 0;
          font-size: 16px;
          &:nth-child(1) {
            left: 0;
          }
          &:nth-child(2) {
            left: 18px;
          }
          &:nth-child(3) {
            left: 36px;
          }
          &:nth-child(4) {
            left: 54px;
          }
          &:nth-child(5) {
            left: 72px;
          }
        }
      }
      .noactivestar {
        height: 18px;
        position: relative;
        color: #e5e5e5;
      }
      .activestar {
        position: absolute;
        left: 0;
        top: 0;
        width: 0;
        height: 18px;
        color: #FEDA00;
        overflow: hidden;
      }
      .starscore {
        width: 24px;
        height: 18px;
        text-align: center;
        line-height: 18px;
        font-size: 14px;
        color: #fdc03d;
      }
    }

    .coming_movie{
      margin:30px 0px;
      .movielist_Box {
        width: 1300px;
      }
      .buyinfo {
        width:70px;
        text-align: center;
        color: #fff;
        font-size: 13px;
        margin:0 auto;
        .buybtn {
          height: 40px;
        }
      }
    }

    .movieTitle {
      padding-bottom: 2px;
      font-size: 18px;
      color: #000;
      font-weight: 600;
      font-family: "微软雅黑";
      box-shadow:0px 2px 3px #bbb;
      text-indent: 6px;

      >.total {
        font-size: 16px;
        font-weight: 400;
        i {
          display: block;
          width: 20px;
          height: 20px;
          margin-top: 2px;
          // background-color: #f5f5f5;
          background: url("../../assets/images/right.png") no-repeat center
            center;
        }
      }
    }
    .movieList {
      margin-top: 10px;
      overflow-x: auto;
      overflow-y: hidden;
      .movielist_Box {
        .movie_Item {
          float: left;
          width: 120px;
          margin-right: 10px;
          .movie_img {
            border-radius: 10px;
            overflow: hidden;
            height: 162px;
          }
          .movie_name {
            padding: 4px 0;
            text-align: center;
            font-size: 15px;
            font-weight: bold;
            font-family: "微软雅黑";
          }
        }
        >.movie_Item:last-child {
          margin-right: 0px;
        }
      }
    }
  }

}
</style>